@use "../component/navigation";
@use "../abstract/index" as *;

body {
  min-height: 100vh;
  font-family: "Barlow";
  background: url(../../images/destination/background-destination-mobile.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  // padding-bottom: 3rem;
}

.container {
  max-width: 1110px;
  margin-inline: auto;
  text-align: center;
  padding-inline: 24px;
  padding-bottom: 42px;
}

.destination__subtitle {
  font-family: "Barlow Condensed", sans-serif;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  text-transform: uppercase;
  font-weight: normal;
  color: color(secondary-color);
}

.destination__subtitle--number {
  margin-right: 16px;
  opacity: 0.25;
  font-weight: 700;
}

.destination__image {
  width: 170px;
  height: 170px;
  margin-top: 32px;
  margin-bottom: 26px;
  margin-inline: auto;

  img {
    display: block;
  }
}

.destination__nav__list {
  padding: 0;
  list-style: none;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 26px;
}

.destination__link {
  position: relative;

  button {
    background: none;
    border: none;
    padding: 0;
    margin: 0;
    cursor: pointer;
    font-family: "Barlow Condensed", sans-serif;
    color: color(accent-color);
    text-transform: uppercase;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }

  &:before {
    content: "";
    position: absolute;
    inset: auto 0 -11px 0;
    height: 3px;
    background: color(secondary-color);
    transform: scale(0);
    transform-origin: center;
    transition: transform 0.15s, opacity 0.1s ease-in;
  }

  &:hover {
    &:before {
      opacity: 0.25;
      transform: scaleX(1);
    }
  }
  &.active {
    button {
      color: color(secondary-color);
    }

    &:before {
      transform: scaleX(1);
    }

    &:hover {
      &:before {
        opacity: 1;
      }
    }
  }
}

.destination__title {
  font-family: "Bellefair", sans-serif;
  color: color(secondary-color);
  text-transform: uppercase;
  font-size: 56px;
  font-weight: normal;
  line-height: 64px;
  margin-top: 31px;
}

.destination__text {
  position: relative;
  font-family: "Barlow", sans-serif;
  font-size: 15px;
  line-height: 25px;
  font-weight: 400;
  color: color(accent-color);

  &:before {
    position: absolute;
    content: "";
    height: 1px;
    inset: auto 0 -32px 0;
    background: #383b4b;
  }
}

.destination__distance {
  margin-top: 65px;
  margin-bottom: 32px;
}

.distination__state {
  text-align: center;
  .destination__state__text {
    font-family: "Barlow Condensed", sans-serif;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
    color: color(accent-color);
    text-transform: uppercase;
    margin-bottom: 12px;
  }

  .destination__state__number {
    font-family: "Bellefair", sans-serif;
    font-size: 28px;
    line-height: 32.09px;
    color: color(secondary-color);
    text-transform: uppercase;
  }
}

@keyframes scale {
  0% {
    opacity: 0;
    transform: scaleX(0);
  }

  100% {
    opacity: 1;
    transform: scaleX(1);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes zoomIn {
  0% {
    opacity: 0;
    transform: scale(0);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

#fade-in {
  animation: scale 0.5s ease-in;
  img {
    animation: rotate 0.7s;
  }
}

.scale {
  animation: scale 0.4s;
}

.zoomIn {
  @media (max-width: 1099px) {
    margin-inline: auto;
  }
  width: fit-content;
  animation: zoomIn 0.4s;
}

@media (min-width: 768px) {
  body {
    background: url(../../images/destination/background-destination-tablet.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .container {
    padding: 40px 38px;
  }

  .destination__subtitle {
    width: fit-content;
    margin-right: auto;
    margin-left: 0;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }

  .destination__image {
    width: 300px;
    height: 300px;
    margin-top: 60px;
    margin-bottom: 53px;
  }

  .destination__nav__list {
    column-gap: 34px;
  }

  .destination__link {
    button {
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 2.7px;
    }
    &:before {
      inset: auto 0 -15px 0;
      height: 3px;
    }
  }
  .destination__title {
    font-size: 80px;
    line-height: 92px;
    margin-top: 47px;
    margin-bottom: 8px;
  }

  .destination__text {
    margin-inline: auto;
    font-size: 16px;
    line-height: 28px;
    max-width: 573px;

    &:before {
      inset: auto 0 -50px 0;
    }
  }

  .destination__info {
    margin-top: 78px;
    display: grid;
    grid-template-columns: 220px 220px;
    column-gap: 11px;
    justify-content: center;
  }

  .destination__distance {
    margin: 0;
  }
}

@media (min-width: 1100px) {
  body {
    padding-top: 0.05px;
    background: url(../../images/destination/background-destination-desktop.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }

  .container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 10%;
    padding: 76px 55px 0;
    text-align: left;

    @media (min-width: 1300px) {
      column-gap: 14.14%;
      padding-inline: 0;
    }
  }

  .destination__subtitle {
    width: fit-content;
    margin-right: auto;
    margin-left: 0;
    font-size: 28px;
    line-height: 34px;
    letter-spacing: 4.725px;

    .destination__subtitle--number {
      margin-right: 24px;
    }
  }

  .destination__image {
    width: 445px;
    height: 445px;
    margin-top: 97px;
    margin-bottom: 53px;
    margin-left: 64px;
  }

  .destination__right {
    margin-top: 98px;
  }

  .destination__nav__list {
    justify-content: start;
    margin: 0;
  }

  .destination__title {
    font-size: 100px;
    line-height: 115px;
    margin-top: 52px;
    margin-bottom: 14px;
  }

  .destination__text {
    margin: 0 auto 0 0;
    max-width: 444px;
    font-size: 18px;
    line-height: 32px;

    &:before {
      inset: auto 0 -55px 0;
    }
  }

  .destination__info {
    margin-top: 83px;
    display: grid;
    grid-template-columns: auto auto;
    column-gap: 79px;
    justify-content: start;
  }
  .distination__state {
    text-align: left;
  }
}
